export default {
    props: {
        event: {
            type: Object,
            default: _ => {},
        },
        isShowIcon: {
            type: Boolean,
            default: _ => true,
        },
    },
    computed: {
        date() {
            if(!this.event || !this.event.date)
                return ''
            let format = this.isENG ? 'MMMM D, YYYY' : 'D MMMM, YYYY'
            return this.$moment(this.event.date).format(format)
        },
        img() {
            if(this.get_('image1'))
                return '/static/pictures/dt/' + this.get_('image1')
        },
        imgs() {
            let imgs = this.get_('images')
            imgs = imgs ? imgs.split('; ') : []
            if(imgs.length == 0 && this.event.paper)
                imgs.push('events/paper.png')
            imgs = imgs.map(img => '/static/pictures/dt/' + img)
            return imgs
        },
        title() {
            if(this.get_('title'))
                return this.get_('title')
            if(this.event.paper && this.event.paper.journal && this.isENG)
                return `New paper "${this.event.paper.title}"`
            if(this.event.paper && this.event.paper.journal && this.isRUS)
                return `Новая публикация "${this.event.paper.title}"`
            if(this.event.paper && this.event.paper.conference && this.isENG)
                return `${this.event.paper.conference.name} paper accepted`
            if(this.event.paper && this.event.paper.conference && this.isRUS)
                return `Публикация на ${this.event.paper.conference.name}`
            return ''
        },
        description() {
            if(this.get_('description'))
                return this.get_('description')
            return this.abstract
        },
        isShowPaperInfo() {
            if(!this.event || !this.event.paper)
                return false
            if(this.event.paper.conference)
                return true
            if(!this.event.paper.journal)
                return false
            if(this.event.paper.journal.name == 'ArXiv preprint')
                return false
            return true
        }
    },
    methods: {
        get_(opt) {
            if(this.isRUS && this.event[opt + '_rus'] !== undefined)
                return this.event[opt + '_rus']
            return this.event[opt]
        },
    },
}



