<script>

import EventMixin from './_EventMixin'

export default {
    name: 'dt-event-thumb',
    mixins: [EventMixin],
}
</script>

<template lang="pug">

dkbr-frame.dt-event-thumb(
    th="th7"
    :isThInv="true"
    :elevation="10")
    div.dt-event-thumb__header
        dkbr-icon.dt-event-thumb__header-date(
            :isThInv="true"
            name="date_range"
            :text="date")
        dkbr-button.dt-event-thumb__header-more(
            v-if="isShowIcon"
            :isThInv="true"
            :isRaised="false"
            :isDense="true"
            icon="info"
            @click.native="$emit('select')")
    div.dt-event-thumb__title(
        tag="div",
        @click="$emit('select')") {{ title }}
</template>

<style lang="sass">

@import '~@dt/sass/root.sass'

.dt-event-thumb
    +flex(column)
    +padding(10px 10px 2px)
    +border(light-top)
    &__header
        +padding(2px)
        +flex(row, nowrap, space-between, center)
        +font(extra)
        +color-font(light3)
        &_small
            +font(extra-small)
    &__title
        //flex: 1 0 100px
        +padding(5px 0 20px)
        +color-font(dark3)
        +font(base-bold)
        text-decoration: underline
        cursor: pointer
        &:hover
            +color-font(light3)
            text-decoration: none
</style>



