<script>

import EventsList from '@dt/components/events/EventsList'
import PersonThumb from '@dt/components/team/PersonThumb'

export default {
    name: 'dt-page-index',
    components: {
        'dt-events-list': EventsList,
        'dt-person-thumb': PersonThumb,
    },
    data() {
        return {
            general: {
                en:
                    'Laboratory Tensor networks and deep learning for applications in data mining under the guidance of Prof. Andrzej Cichocki on the basis of the <a class="dt-link" href="http://www.skoltech.ru/en/" target="_blank">Skolkovo Institute of Science and Technology</a> develops new fundamental approaches for training, testing and storing parameters of deep neural networks based on tensor decomposition techniques. These approaches allow to reduce by orders of magnitude computational complexity and required memory for the operation of the network, while maintaining a high quality of prediction.',
                ru:
                    'Лаборатория Тензорные сети и глубинное обучение для интеллектуального анализа данных под руководством Проф. Анджея Чихоцкого на базе  <a class="dt-link" href="http://www.skoltech.ru/?lang=ru" target="_blank">Сколковского института науки и технологий</a> занимается разработкой новых подходов, основанных на тензорных разложениях, для построения, обучения, использования и хранения параметров глубинных нейронных сетей, позволяющих на порядки снизить вычислительную сложность и объемы памяти, требуемые для работы с сетью, при высоком качестве предсказания.',
            },
            mission: {
                en:
                    'The Laboratory mission is to pursue cutting-edge research in the design and analysis of deep neural networks, tensor decompositions, tensor networks and multiway analysis with many potential practical applications.',
                ru:
                    'Основная задача Лаборатории - проведение передовых исследований в области разработки и анализа глубинных нейронных сетей, тензорных разложений, тензорных сетей и многофакторного анализа с большим количеством потенциальных практических применений.',
            },
            command: {
                en:
                    'The Laboratory brings together several professors and young researchers in the fields of machine learning, computer vision, artificial intelligence, robotics, large scale data analysis, mathematics as well as computational neuroscience and bioinformatics.',
                ru:
                    'Наш коллектив объединяет профессоров и молодых исследователей, являющихся специалистами мирового уровня в области машинного обучения, компьютерного зрения, искусственного интеллекта, робототехники, анализа больших объемов данных, а также вычислительной математики, нейробиологии и биоинформатики.',
            },
        }
    },
}
</script>

<template lang="pug">

div.dt-page-index
    div.dt-page-index__main
        dt-person-thumb.dt-page-index__main-image(
            :person="teamTbl.isLoaded ? teamTbl.row('cichocki') : undefined"
            :isHead="true"
            @select="$router.push({name: 'dt-page-team', params: {id: 'cichocki'}})")
        section.dt-page-index__main-text1
            p.dt-page-index__text.dt-text(
                v-html="general[LANG]")
        section.dt-page-index__main-text2
            p.dt-page-index__text.dt-text(
                v-html="mission[LANG]")
            p.dt-page-index__text.dt-text(
                v-html="command[LANG]")
    dt-events-list.dt-page-index__aside(
        :isOnlyImportant="true"
        :isShowThumbIcon="false")
</template>

<style lang="sass">

@import '~@dt/sass/root.sass'

.dt-page-index
    +flex(row, nowrap, space-between)
    +flex-direction($nano: column)
    &__main
        +flex(row, wrap, flex-start, flex-start, flex-start)
        &-image
            flex: 1 1 250px
            +flex-order(10, $milli: 21)
            +margin(10px, 15px, $nano: 15px 20px)
            text-align: center
        &-text1
            flex: 2 1 250px
            +flex-order(20)
            align-self: flex-start
        &-text2
            +flex-order(30)
    &__text
        +padding(10px)
    &__aside
        flex: 0 0 300px
        //+width(300px)
        //+flex-basis(300px, 250px, 250px, 200px, auto)
        +flex-basis(300px, $nano: auto)
        +margin(0 0 0 30px, $micro: 0 0 0 15px, $nano: 20px 10px 15px auto)
</style>



