export default {
    props: {
        id: {
            type: String,
            default: _ => '',
        },
        isOnlyImportant: {
            type: Boolean,
            default: _ => false,
        },
        isShowThumbIcon: {
            type: Boolean,
            default: _ => true,
        },
    },
    computed: {
        event_() {
            if(this.id)
                return this.eventsTbl.row(this.id)
        },
        events() {
            return this.eventsTbl.rows.filter(e =>
                e.active && (e.important || !this.isOnlyImportant))
        },
        ids() {
            return this.events.map(e => e.id)
        },
        titlesShort() {
            return this.events
                .map(e => this.isENG ? e.title_short : e.title_short_rus)
        },
    },
    methods: {
        select(id=undefined) {
            this.$router.push({name: 'dt-page-events', params: {id: id}})
        },
    },
}



