<script>

import EventsMixin from './_EventsMixin'
import EventThumb from './EventThumb'

export default {
    name: 'dt-events-list',
    mixins: [EventsMixin],
    components: {
        'dt-event-thumb': EventThumb,
    },
}
</script>

<template lang="pug">

div.dt-events-list
    dkbr-progress.dt-events-list__progress(
        v-if="!eventsTbl.isLoaded || !eventsTbl.isParsed"
        :isThInv="eventsTbl.isLoaded ? false : true"
        :isInf="true")
    dt-event-thumb.dt-events-list__event(
        v-for="event of events"
        :key="event.id"
        :event="event"
        :isShowIcon="isShowThumbIcon"
        @select="select(event.id)")
</template>

<style lang="sass">

@import '~@dt/sass/root.sass'

.dt-events-list
    width: 100%
    +flex(row, wrap, space-around)
    &__progress
        position: absolute
        margin: 0
        padding: 0
    &__event
        flex: 1 1 250px
        +margin(10px)
</style>



